import {
	customElement,
	html,
	nothing,
	property,
} from '@umbraco-cms/backoffice/external/lit';
import { UmbLitElement } from '@umbraco-cms/backoffice/lit-element';
import { SyncExportSelectedItem } from '../types';

@customElement('usync-exporter-selection')
export class uSyncExporterSelection extends UmbLitElement {
	@property({ type: Array })
	selection: SyncExportSelectedItem[] = [];

	#remove(item: SyncExportSelectedItem) {
		// dispatch event to remove item
		this.dispatchEvent(new CustomEvent('remove', { detail: item }));
	}

	#toggleAncestors(item: SyncExportSelectedItem) {
		item.includeAncestors = !item.includeAncestors;
		this.dispatchEvent(new CustomEvent('update', { detail: item }));
	}

	#toggleChildren(item: SyncExportSelectedItem) {
		item.includeChildren = !item.includeChildren;
		this.dispatchEvent(new CustomEvent('update', { detail: item }));
	}

	#toggleDependencies(item: SyncExportSelectedItem) {
		item.includeDependencies = !item.includeDependencies;
		this.dispatchEvent(new CustomEvent('update', { detail: item }));
	}

	render() {
		var items = this.selection.map((item) => {
			return html`
				<uui-table-row>
					<uui-table-cell><umb-icon .name=${item.icon}></umb-icon></uui-table-cell>
					<uui-table-cell>${item.entityType}</uui-table-cell>
					<uui-table-cell>${item.name}</uui-table-cell>
					<uui-table-cell>
						<uui-toggle
							?checked=${item.includeChildren}
							@change=${() => this.#toggleChildren(item)}
							label="include children">
							<div slot="label"></div>
						</uui-toggle>
					</uui-table-cell>
					<uui-table-cell>
						<uui-toggle
							?checked=${item.includeDependencies}
							@change=${() => this.#toggleDependencies(item)}
							label="Include dependencies">
							<div slot="label"></div>
						</uui-toggle>
					</uui-table-cell>
					<uui-table-cell>
						<uui-toggle
							?checked=${item.includeAncestors}
							@change=${() => this.#toggleAncestors(item)}
							label="Include ancestors">
							<div slot="label"></div>
						</uui-toggle>
					</uui-table-cell>
					<uui-table-cell>
						<uui-button @click=${() => this.#remove(item)} label="Remove"
							><umb-icon name="icon-trash" color="red"></umb-icon
						></uui-button>
					</uui-table-cell>
				</uui-table-row>
			`;
		});

		return this.selection.length === 0
			? nothing
			: html`<uui-table>
					<uui-table-head>
						<uui-table-head-cell
							><umb-icon name="icon-flag"></umb-icon
						></uui-table-head-cell>
						<uui-table-head-cell>Type</uui-table-head-cell>
						<uui-table-head-cell>Name</uui-table-head-cell>
						<uui-table-head-cell>Children</uui-table-head-cell>
						<uui-table-head-cell>Dependencies</uui-table-head-cell>
						<uui-table-head-cell>Ancestors</uui-table-head-cell>
						<uui-table-head-cell></uui-table-head-cell>
					</uui-table-head>
					${items}
				</uui-table>`;
	}
}

export default uSyncExporterSelection;
