import {
	css,
	customElement,
	html,
	nothing,
	property,
	query,
	state,
} from '@umbraco-cms/backoffice/external/lit';
import { UmbLitElement } from '@umbraco-cms/backoffice/lit-element';

@customElement('usync-exporter-file-upload')
export class SyncExporterFileUploadElement extends UmbLitElement {
	@property({ type: String })
	label = 'Upload File';

	@property({ type: String })
	accept = '';

	@query('#file')
	private _fileInput!: HTMLInputElement;

	@state()
	private _file?: File;

	private async _getFile(fileEntry: FileSystemFileEntry): Promise<File> {
		return new Promise((resolve, reject) => {
			fileEntry.file(resolve, reject);
		});
	}

	private fireChange() {
		this.dispatchEvent(
			new CustomEvent('change', {
				detail: {
					file: this._file,
				},
			}),
		);
	}

	private async _onFilePickerChange() {
		const files = this._fileInput.files ? Array.from(this._fileInput.files) : [];
		const entry = files[0];
		const isFile = entry instanceof File;
		const file = isFile ? entry : await this._getFile(entry);

		this._file = file;
		this.fireChange();
	}

	private async _onRemoveFile() {
		this._file = undefined;
		this._fileInput.value = '';
		this.fireChange();
	}

	private _onUpload() {
		this._fileInput.click();
	}

	render() {
		return html`<input
				@click=${this._onUpload}
				type="file"
				id="file"
				accept="${this.accept}"
				@change=${this._onFilePickerChange} />
			${this._renderFile()} ${this._renderButton()}`;
	}

	private _renderFile() {
		if (!this._file) return nothing;

		return html`<div class="file">
			<div>${this._file.name}</div>
			<uui-button @click=${this._onRemoveFile} compact color="danger" label="Remove">
				<uui-icon name="icon-trash"></uui-icon>
			</uui-button>
		</div>`;
	}

	private _renderButton() {
		if (this._file) return nothing;
		return html`<uui-button
			look="placeholder"
			label="${this.label}"
			@click=${this._onUpload}></uui-button>`;
	}

	static styles = [
		css`
			.file {
				display: flex;
				align-items: center;
				gap: var(--uui-size-space-2);
			}

			#file {
				display: none;
			}
		`,
	];
}

export default SyncExporterFileUploadElement;

declare global {
	interface HTMLElementTagNameMap {
		'usync-exporter-file-upload': SyncExporterFileUploadElement;
	}
}
