import { UMB_AUTH_CONTEXT } from '@umbraco-cms/backoffice/auth';
import { UmbEntryPointOnInit } from '@umbraco-cms/backoffice/extension-api';

import { manifests as treeManifests } from './tree/manifest';
import { manifests as workspace } from './workspace/manifest';
import { manifests as processing } from './processing/manifest';
import { manifests as localization } from './lang/manifest';

import { client } from './api/client.gen';

import './workspace/components';
import './processing/components';

export const onInit: UmbEntryPointOnInit = (_host, extensionRegistry) => {
	extensionRegistry.registerMany([
		...treeManifests,
		...workspace,
		...processing,
		...localization,
	]);

	_host.consumeContext(UMB_AUTH_CONTEXT, (_auth) => {
		if (!_auth) return;
		const config = _auth.getOpenApiConfiguration();

		client.setConfig({
			baseUrl: config.base,
			credentials: config.credentials,
		});

		client.interceptors.request.use(async (request, _options) => {
			const token = await _auth.getLatestToken();
			request.headers.set('Authorization', `Bearer ${token}`);
			return request;
		});
	});
};
