import uSyncExporter from '../constants';

const workspaceAlias = uSyncExporter.workspace;

const workspace: UmbExtensionManifest = {
	type: 'workspace',
	alias: workspaceAlias,
	name: 'uSync Exporter',
	js: () => import('./workspace.element'),
	meta: {
		entityType: uSyncExporter.element,
	},
};

const workspaceContext: UmbExtensionManifest = {
	type: 'workspaceContext',
	alias: uSyncExporter.context,
	name: 'uSync Exporter Context',
	js: () => import('./workspace.context'),
	conditions: [
		{
			alias: 'Umb.Condition.WorkspaceAlias',
			match: workspaceAlias,
		},
	],
};

const workspaceViews: Array<UmbExtensionManifest> = [
	{
		type: 'workspaceView',
		alias: 'usync.exporter.view.default',
		name: 'uSync Exporter default view',
		js: () => import('./views/default.element'),
		weight: 200,
		meta: {
			label: 'Export',
			pathname: 'export',
			icon: uSyncExporter.icon,
		},
		conditions: [
			{
				alias: 'Umb.Condition.WorkspaceAlias',
				match: workspaceAlias,
			},
		],
	},
	{
		type: 'workspaceView',
		alias: 'usync.exporter.view.import',
		name: 'uSync Exporter import view',
		js: () => import('./views/import.element'),
		weight: 100,
		meta: {
			label: 'Import',
			pathname: 'import',
			icon: uSyncExporter.importIcon,
		},
		conditions: [
			{
				alias: 'Umb.Condition.WorkspaceAlias',
				match: workspaceAlias,
			},
		],
	},
];

export const manifests = [workspace, workspaceContext, ...workspaceViews];
